import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'initial'
})
export class InitialPipe implements PipeTransform {

  transform(input,gender) {
    let output='';
    if(gender=='male'){
      output='Mr.'+input;
    }
    else{
      output='Miss.'+input
    }
    return output;
  }

}
