import { Component, OnInit, OnChanges } from '@angular/core';
import { EventEmitter } from '@angular/core';

@Component({
  selector: 'app-child1',
  templateUrl: './child1.component.html',
  styleUrls: ['./child1.component.css'],
  inputs: ['xParent', 'yParent', 'child2'],
  outputs: ['aEvent', 'bEvent']
})                                     //OnInit,OnChanges are interfaces
export class Child1Component implements OnInit, OnChanges {

  constructor() { }

  ngOnInit() {
    console.log("I am child oninit");
  }
  ngOnChanges(obj){
    console.log("I am child onchange");
    console.log(obj);
  }
  a = 4;
  b = "Sachin";
  c=['aa','bb'];
  aEvent = new EventEmitter();
  bEvent = new EventEmitter();
  sendData() {
    this.aEvent.emit(this.a);
    this.bEvent.emit(this.b);
  }
}
