import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'search'
})
export class SearchPipe implements PipeTransform {

  transform(input,arg) {
    if(!input)return null;
    if(!arg)return input;
    arg = arg.toLowerCase();
    return input.filter(function(item){
        return JSON.stringify(item).toLowerCase().includes(arg);
    });
  }

}
