import { Pipe, PipeTransform } from '@angular/core';
import { compileNgModuleFromRender2 } from '@angular/compiler/src/render3/r3_module_compiler';

@Pipe({
  name: 'ordinal'
})
export class OrdinalPipe implements PipeTransform {

  transform(num1) {
    let num=num1%10;
    let num2='';
    if(!num1){
      num2="";
    }
    else if(num==1)
    {
     num2= num1+"st";
    }
    else if(num==2)
    {
    num2 =num1+"nd";
    }
    else if(num==3)
    {
    num2=num1+"rd";
    }
    else
    {
    num2=num1+"th";
    }
    return num2;

  }

}
